.details{
  position: relative;
  z-index: 20;
  max-width: 768px;
  margin:0 auto 1rem;
  width: 100%;

  &__row{
      width: 100%;
      display: flex;
      align-items: center;
      border-bottom: 1px solid rgba(#fff,0.25);
      justify-content: space-between;
      padding:0.5rem 0;
  }

  &__item{
      text-align: left;
      h6{
          text-transform: uppercase;
          font-size: 14px;
          font-weight: bold;
          margin:0;
      }

      h3{
          margin:0.15rem 0;
      }
  }
}